#mainmenu-container .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: #f0f0f0;
}

#mainmenu-container .feet {
    line-height: 60px;
    padding-left: 40px;
}

#mainmenu-container .splash {
    padding: 75px;
    display: block;
    margin: auto;
    max-width: 100%;
    height: auto;
}

#mainmenu-container .btn {
    background-color: #f8f4f4;
}

#mainmenu-container img {
    border-radius: 100%;
}